import {
  FETCH_POSTS,
  SHOW_POSTS,
  CHANGE_QTY_POSTS,
  SHOW_WEATHER,
  SEARCH_POST
} from "../actionTypes/actionTypes";

export function changeQtyPosts(dispatch, qty) {
 dispatch({type: CHANGE_QTY_POSTS, payload: qty})
 
}
  
export function fetchPosts(dispatch, qtyPosts) {

    fetch(`http://localhost:4200/posts?_start=0&_end=15`)
    .then(response => response.json())
    .then(data =>
      dispatch({type: FETCH_POSTS, payload: data }),
    );
  }

export default {fetchPosts, changeQtyPosts}