import {
  FETCH_POSTS,
	SHOW_POSTS,
  SHOW_WEATHER,
  SEARCH_POST,
  CHANGE_QTY_POSTS
} from "../actionTypes/actionTypes";

const initialState = {
  loading: true,
  fetchedPosts: [],
  outputedPosts: [],
  postQty: 6,
  weatherValue: '25',
  searchValue: ''
}

const postsReducer = (state = initialState, action) => {
  switch (action.type) {
    case FETCH_POSTS:
      return {
        ...state,
        loading: false,
        fetchedPosts: action.payload
        
      }
    case SHOW_WEATHER:
      return {
        ...state,
        weatherValue: action.payload
      }
    case SEARCH_POST: 
    return {
      ...state,
      searchValue: action.payload
    }
    case CHANGE_QTY_POSTS:
      return {
        ...state,
        postQty: action.payload
      }
    default:
      return state
  }
}

export default postsReducer;