import classes from './Posts.module.scss';
import PostCard from '../PostCard/PostCard';
import {useEffect, useState} from 'react';
import {useSelector, useDispatch} from "react-redux";
import Loader from '../Loader/Loader';
import Banner from '../Banner/Banner';
import {fetchPosts, changeQtyPosts} from '../../store/actions/postsActions';

export default function Posts() {
  const [count, setCount] = useState(6);

  const bannerQuery = 5;

  const dispatch = useDispatch();
	const weatherValue = useSelector(state => state.weatherValue);
  const posts = useSelector(state => state.fetchedPosts);
  const loading = useSelector(state => state.loading);
  const handleScroll = () => {
    if ((window.innerHeight + window.pageYOffset) >= document.body.offsetHeight) {
      setCount(count+6)
      fetchPosts(dispatch, count, 0);
    }
}

  useEffect(() => {
    fetchPosts(dispatch, count, 0);
    document.addEventListener("scroll", handleScroll)
    return () => {
      document.removeEventListener("scroll", handleScroll)
    }

  }, [count]);

  return (
    <div className={classes.Posts}>
     {
      loading ? <Loader/> : 

      posts.slice(0, count).map( (post)=> 
      posts.indexOf(post) === bannerQuery ?
      <>
      <PostCard key={post.id} post = {post}/>
      <Banner/> 
      </>
      :

      <PostCard key={post.id} post = {post}/>
      )
    }
    
    </div>
  )
}
